  nav ul li{
    list-style-type: none;
    display: inline-block;
    transition: 0.9s all;
    text-transform: uppercase;
    font-weight: bold;
    text-align: center;
    text-decoration: none;
    /* make the width same of each button */
    font-size: 1.5rem;
  }
  nav ul li:hover{
    background-color:rgba(23, 38, 150, 0.795);
  }
  nav ul li a{
    text-decoration: none;
    color: #fff;
    padding: 30px;
   
